import React from "react";
import { observer, inject } from "mobx-react";
import { List, Icon, InputItem, Button, Toast, WhiteSpace, WingBlank } from "antd-mobile";

require("../style.scss");

import EasyNarBar from "@/components/EasyNarBar";

@inject((stores: any) => ({
  store: stores.passwordStore
}))
@observer
class Password extends React.Component<any> {
  componentWillUnmount() {
    this.props.store.clearData();
  }
  savePassword = () => {
    const { savePassword } = this.props.store;
    savePassword(this.ToLogin);
  };
  ToLogin = (wxCard: string, wxUser: string, clientToken: string) => {
    const { history } = this.props;

    history.push(`/login?wxCard=${wxCard}&wxUser=${wxUser}&clientToken=${clientToken}`);
  };
  render() {
    const { oldPassword, newPassword, repeatPassword, setPassword, savePassword, loading } = this.props.store;
    return (
      <>
        <EasyNarBar
          mainContent="修改密码"
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            this.props.history.goBack();
          }}
        />
        <List>
          <InputItem
            className="input"
            type="password"
            placeholder="请输入原来的密码"
            value={oldPassword}
            onChange={val => setPassword("oldPassword", val)}
            maxLength={30}
          >
            <span>原密码</span>
          </InputItem>

          <InputItem
            className="input"
            type="password"
            value={newPassword}
            onChange={val => setPassword("newPassword", val)}
            placeholder="请输入新的密码"
            maxLength={30}
          >
            <span>新密码</span>
          </InputItem>

          <InputItem
            className="input"
            type="password"
            value={repeatPassword}
            onChange={val => setPassword("repeatPassword", val)}
            placeholder="请输入新的密码"
            maxLength={30}
          >
            <span>确认密码</span>
          </InputItem>
        </List>
        <WhiteSpace size="xl" />
        <WingBlank>
          <Button onClick={this.savePassword} className="button" type="primary" disabled={loading}>
            确定
          </Button>
        </WingBlank>
      </>
    );
  }
}

export default Password;
